.loader {
  --first-color: #ff7473;
  --second-color: #ffc952;
  --third-color: #47b8e0;
  --size: 200px;
  --duration: 1s;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
}

.loader__content {
  width: var(--size);
  height: var(--size);
  position: relative;
}

.loader__first, .loader__second, .loader__third {
  width: 100%;
  height: 100%;
  animation: var(--duration) linear infinite;
  border-radius: 50%;
  position: absolute;
}

.loader__first {
  border-bottom: 3px solid var(--first-color);
  animation-name: first;
}

.loader__second {
  border-right: 3px solid var(--second-color);
  animation-name: second;
}

.loader__third {
  border-top: 3px solid var(--third-color);
  animation-name: third;
}

@keyframes first {
  0% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(360deg);
  }
}

@keyframes second {
  0% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(360deg);
  }
}

@keyframes third {
  0% {
    transform: rotateX(50deg)rotateY(55deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg)rotateY(55deg)rotateZ(360deg);
  }
}

